.container {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;  
}

.about-me {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 2vw;
    max-width: 50%;
    color: white;
    font-size: 2vw;
    float: left;
    line-height: 4vw;
}

.image-container {
    background-position: center right;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100vh;
    background-image: url('../../resources/howard-picture.jpg');
}


